.leaders {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding-top: 20px;
  padding-right: 24px;
  padding-left: 24px;
  text-align: left;
  background: radial-gradient(
    70.24% 70.24% at 49.87% 29.76%,
    rgba(18, 12, 1, 0.9) 0%,
    #000000 100%
  );

  @media (orientation: landscape) {
    text-align: center;
  }
}

.leaders__title {
  @include title;
  margin-bottom: 8px;
}

.leaders__subtitle {
  @include subtitle;
}

.leaders__list {
  @include clear-list;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  height: 100%;
}

.leaders__item {
  &-first-place {
    position: relative;
    z-index: 2;
    order: 2;

    .leader__avatar-wrapper::before {
      content: '👑';

      position: absolute;
      z-index: 1;
      top: -22px;
      left: calc(50% - 5px);
      @include size(32px, 32px);
      font-size: 32px;
      line-height: 32px;
      transform: translateX(-50%);
    }
  }

  &-second-place {
    z-index: 1;
    order: 3;
    margin-left: -16px;
    text-align: right;

    & .leader {
      padding-left: 8px;
    }
  }

  &-third-place {
    z-index: 1;
    order: 1;
    margin-right: -16px;

    & .leader {
      padding-right: 8px;
    }
  }

  &-fourth-place {
    display: none;
  }

  &-choosen-user {
    display: none;

    .leader__avatar-wrapper::before {
      content: '👍';

      position: absolute;
      z-index: 1;
      top: -22px;
      left: calc(50% - 5px);
      @include size(32px, 32px);
      font-size: 32px;
      line-height: 32px;
      transform: translateX(-50%);
    }
  }

  @media (orientation: landscape) {
    &-first-place {
      order: 3;
    }

    &-second-place {
      order: 4;
      // margin-left: -16px;
    }

    &-third-place {
      order: 2;
      // margin-right: -16px;
    }

    &-fourth-place {
      order: 5;
      display: block;
      margin-left: -16px;

      & .leader {
        padding-left: 8px;
      }
    }

    &-choosen-user {
      order: 1;
      display: block;
      margin-right: -16px;

      & .leader {
        padding-right: 8px;
      }
    }
  }
}
