.leader {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  text-align: center;
}

.leader__user {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 16px;
}

.leader__avatar-wrapper {
  position: relative;
}

.leader__avatar {
  @include size(64px, 64px);
  border-radius: 50%;
  filter: grayscale(1);
}

