.leader__name {
  @include clear-block;
  max-width: 120px;
  margin-bottom: 4px;
  @include font(500, 16px, 18px, $font-roboto);
  color: $color-white;
}

.leader__score {
  @include clear-block;

  @include font(400, 14px, 16px, $font-roboto);
  color: $color-grey-light;
}

.leader__bar {
  display: block;
  width: 120px;
  padding-top: 18px;
  background: linear-gradient(
      180deg,
      rgba(0, 0, 0, 0) -2.22%,
      rgba(0, 0, 0, 0.8) 100%
    ),
    radial-gradient(
      149.08% 95.38% at 38.75% 71.48%,
      #000000 0%,
      #231900 0.01%,
      #4d4d4d 100%
    );
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.2),
    inset 2px 2px 16px rgba(103, 103, 103, 0.6);
  border-radius: 6px 6px 0 0;
}

.leader--winner .leader__bar {
  background: radial-gradient(
    91.67% 122.17% at 69.17% -11.17%,
    #ffa300 0%,
    #2d1c00 100%
  );

  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.4),
    inset 0px 6px 15px rgba(255, 162, 0, 0.4);
}

.leader__bar > span {
  @include font(700, 30px, 32px, $font-roboto);
  color: $color-white;
}
